@import "../../partial/scss/base";
.#{$prefix}wizard {
	height: 100%;
	max-width: 100%;
	overflow-x: hidden;
	overflow-y: hidden;
	.wizard-progress {
		background-color: v(color-secondary-02, $color-secondary-02);
		display: grid;
		grid-template-columns: repeat(
			auto-fit,
			minmax(0, 1fr)
		); //Padrao horizontal
		grid-template-rows: none;
		min-height: 164px;
		overflow-x: auto;
		overflow-y: hidden;
		padding-top: v(spacing-scale-7x);
		width: 100%;
		.wizard-progress-btn {
			background-color: transparent;
			border: none;
			box-shadow: none !important;
			color: var(--interactive);
			font-size: v(font-size-scale-up-01, $font-size-scale-up-01);
			line-height: 19px;
			max-height: 90px;
			padding-bottom: 0;
			padding-top: v(spacing-scale-2x);
			position: relative;
			text-indent: 0;
			transition-delay: 0s;
			transition-duration: 0.15s;
			transition-property: all;
			transition-timing-function: linear;
			&[disabled] {
				color: rgba(var(--interactive-rgb), var(--disabled));
				opacity: 1;
				&::before {
					opacity: var(--disabled);
				}
			}
			&:focus {
				outline: none;
			}
			&.focus-visible:not([disabled]):not(.disabled)::before,
			&:focus-visible:not([disabled]):not(.disabled)::before {
				outline-color: var(--focus-color);
				outline-offset: var(--focus-offset);
				outline-style: var(--focus-style);
				outline-width: var(--focus-width);
			}
			&:hover:not([disabled])::before {
				background-image: linear-gradient(
					rgba(var(--interactive-rgb), var(--hover)),
					rgba(var(--interactive-rgb), var(--hover))
				);
			}
			&:active:not([disabled])::before {
				background-image: linear-gradient(
					rgba(var(--interactive-rgb), var(--pressed)),
					rgba(var(--interactive-rgb), var(--pressed))
				);
			}
			&::before {
				background-color: v(color-secondary-01, $color-secondary-01);
				border: 2px solid var(--interactive);
				border-radius: 50%;
				box-sizing: border-box;
				color: var(--interactive);
				content: attr(step) !important;
				display: block;
				font-size: v(font-size-scale-up-02, $font-size-scale-up-02);
				font-weight: v(font-weight-semi-bold, $font-weight-semi-bold);
				height: 36px;
				left: 50%;
				line-height: 29px;
				margin-top: -13px;
				position: absolute;
				top: 0;
				transform: translateX(-50%);
				transition: all 0.15s linear 0s,
					-webkit-transform 0.15s cubic-bezier(0.05, 1.09, 0.16, 1.4) 0s;
				transition: all 0.15s linear 0s,
					transform 0.15s cubic-bezier(0.05, 1.09, 0.16, 1.4) 0s;
				transition: all 0.15s linear 0s,
					transform 0.15s cubic-bezier(0.05, 1.09, 0.16, 1.4) 0s,
					-webkit-transform 0.15s cubic-bezier(0.05, 1.09, 0.16, 1.4) 0s;
				width: 36px;
				z-index: 3;
			}

			&::after {
				background-color: v(
					color-primary-lighten-01,
					$color-primary-lighten-01
				) !important;
				content: "";
				display: block;
				height: 1px;
				left: calc(-50% + 34px / 2);
				position: absolute;
				top: 5px;
				transition-delay: 0s;
				transition-duration: 0.15s;
				transition-property: all;
				transition-timing-function: linear;
				width: calc(100% - 34px);
				z-index: 1;
			}

			&:first-child::after {
				display: none;
			}

			&[active] {
				color: var(--active);
				font-weight: v(font-weight-semi-bold, $font-weight-semi-bold);
			}

			&[active]::after {
				background-color: var(--active);
				height: 1px;
			}

			&[active]::before {
				background-color: var(--active);
				border: 2px solid var(--color-secondary-01);
				color: v(color-secondary-01, $color-secondary-01);
				font-weight: v(font-weight-bold, $font-weight-bold);
			}
		}
	}

	.wizard-form {
		position: relative;
		height: calc(100% - 164px);
		.wizard-panel {
			position: absolute;
			top: 0;
			left: 1%;
			height: 100%;
			opacity: 0;
			visibility: hidden;
			display: flex;
			flex-wrap: wrap;
			width: 100%;
			margin-bottom: v(spacing-scale-4x);
			&[active] {
				height: 100%;
				opacity: 1;
				visibility: visible;
				transition-property: all;
				transition-duration: 0.5s;
				transition-timing-function: linear;
				transition-delay: 0s;
				left: 0 !important;
			}
			.wizard-panel-content {
				height: auto;
				max-height: calc(100% - 74px);
				flex-grow: 1;
				overflow-y: auto;
				overflow-x: hidden;
				padding: var(--spacing-scale-base) var(--spacing-scale-2x);
				border-top: 1px solid v(color-secondary-04, $color-secondary-04);
				& > :last-child {
					margin-bottom: 220px;
				}
			}
			.wizard-panel-btn {
				width: 100%;
				margin-top: 1px;
				height: fit-content;
				align-self: flex-end;
				padding: v(spacing-scale-2x) 1.5%;
				background-color: v(color-secondary-02, $color-secondary-02);
				border-top: 1px solid v(color-secondary-04, $color-secondary-04);
			}
			.wizard-btn-next,
			.wizard-btn,
			.wizard-btn-prev {
				float: right;
				margin-left: v(spacing-scale-2x);
			}

			.wizard-btn-canc {
				float: left;
			}
		}
	}

	&[vertical] {
		.wizard-progress {
			max-width: 260px;
			height: 100%;
			float: left;
			grid-template-columns: 1fr; //Padr√£o vertical
			text-align: right;
			padding-top: 0;
			position: relative;
			overflow-x: hidden;

			.wizard-progress-btn {
				padding-top: 0;
				padding-bottom: 0px;
				line-height: 100%;
				text-align: right;
				padding-right: 70px;
				height: 100%;
				max-height: 100%;

				&::before {
					left: calc(100% - 32px);
					line-height: 26px;
					margin-top: calc(#{v(spacing-scale-2x)} * -1);
					position: absolute;
					text-align: center;
					top: 50%;
				}

				&:after {
					position: absolute;
					top: calc(-50% + 36px / 2);
					left: calc(100% - 33px);
					width: 1px;
					height: calc(100% - 34px);
				}
				&[active]::after {
					width: 1px;
				}
			}
		}
		&[scroll] {
			.wizard-progress {
				overflow-x: hidden;
				overflow-y: auto;
				.wizard-progress-btn {
					min-height: 100px;
				}
			}
		}
		.wizard-form {
			float: left;
			width: calc(100% - 260px);
			height: 100%;
			.wizard-panel {
				.wizard-panel-content {
					border-top: 0;
				}
			}
		}
	}

	@include media-breakpoint-down(sm) {
		.wizard-progress {
			position: relative;
			padding-top: v(spacing-scale-3x);
			min-height: 100px;
			max-height: 100px;
			transition-property: all;
			transition-duration: 0.25s;
			transition-timing-function: linear;
			transition-delay: 0s;
			overflow-y: hidden;
			.wizard-progress-btn {
				.info {
					font-size: v(
						font-size-scale-down-02,
						$font-size-scale-down-02
					);
					line-height: 1em;
					text-align: center;
				}
			}
			// Ajuste Grip Lines
			&::after {
				background-color: transparent; // v(color-secondary-04, $color-secondary-04);
				bottom: 2px;
				color: var(--interactive);
				content: "\f7a4";
				display: block;
				font-family: "Font Awesome 5 Free", sans-serif;
				font-size: var(--switch-icon-size);
				font-weight: var(--font-weight-black);
				height: 1em;
				left: 50%;
				margin-left: -0.5em;
				position: absolute;
				top: unset;
				transition-delay: 0s;
				transition-duration: 0.25s;
				transition-property: all;
				transition-timing-function: linear;
				width: auto;
			}
		}
		.wizard-form {
			height: calc(100% - 100px);
			.wizard-panel {
				border-top: 0;
				display: flex;
				flex-wrap: wrap;
				margin-bottom: 0;
				max-height: 100%;
				.wizard-panel-content {
					height: auto;
					max-height: 100%;
					padding: var(--spacing-scale-half) var(--spacing-scale-base);
					transition-property: all;
					transition-duration: 0.25s;
					transition-timing-function: linear;
					transition-delay: 0s;
					overflow-y: auto;
					overflow-x: hidden;
					&::-webkit-scrollbar {
						width: var(--spacing-scale-half);
					}
					&::-webkit-scrollbar-track {
						background: var(--color-secondary-02);
						border-radius: var(--spacing-scale-half);
					}
					&::-webkit-scrollbar-thumb {
						background: var(--color-secondary-04);
						border-radius: var(--spacing-scale-half);
					}
				}
				.wizard-panel-btn {
					width: calc(100% - var(--spacing-scale-half));
					//margin-left: -2%;
					padding: 0 !important;
					background-color: var(--bg-color);
					display: flex;
					justify-content: center;
					align-items: flex-end;
					flex-wrap: wrap;
					position: absolute;
					height: fit-content;
					.br-button {
						float: none;
						width: 90%;
						margin: v(spacing-scale-base) 5%;
					}
					.wizard-btn-next,
					.wizard-btn {
						order: 1;
					}
				}
			}
		}

		&[collapsed] {
			.wizard-progress {
				max-height: 64px;
				min-height: 64px;
				overflow-y: hidden;
				padding-bottom: v(spacing-scale-3x);
				padding-top: v(spacing-scale-3x);

				&::after {
					margin-top: v(spacing-scale-4xh);
				}
				.wizard-progress-btn {
					padding-bottom: v(spacing-scale-base);
					.info {
						display: none;
					}
				}
			}
			.wizard-form {
				height: calc(100% - 64px);
				.wizard-panel {
					.wizard-panel-content {
						overflow-x: hidden;
						overflow-y: auto;
					}
				}
			}
		}

		&[vertical] {
			.wizard-progress {
				max-height: 100%;
				max-width: 110px;
				&::after {
					height: 1em;
					left: unset;
					width: 1em;
					right: 2px;
					top: 50%;
					transform: rotate(-90deg);
					margin-top: -0.5em;
				}
				.wizard-progress-btn {
					line-height: 18px;
					padding-right: v(spacing-scale-7x);
					.info {
						line-height: 1.6em;
						margin-top: -0.5em;
						position: absolute;
						right: 54px;
					}
				}
			}
			.wizard-form {
				max-width: calc(100% - 110px);
				min-width: calc(100% - 110px);
				transition-property: all;
				transition-duration: 0.25s;
				transition-timing-function: linear;
				transition-delay: 0s;
				height: 100%;
				.wizard-panel {
					height: 100%;
					max-height: 100%;
				}
			}
			&[collapsed] {
				.wizard-progress {
					max-width: 60px;
					padding-bottom: 0;
					padding-top: 0;
					.wizard-progress-btn {
						padding-right: 0;
						.info {
							display: none;
						}
					}
				}
				.wizard-form {
					max-width: calc(100% - 60px);
					min-width: calc(100% - 60px);
				}
			}
		}
	}
}

